import request from "./request";
import app_url from '@/api/configuration.js'
import store from '@/store'
import md5Libs from "uview-ui/libs/function/md5";
// 全局配置的请求域名
let baseUrl = app_url.baseURL;
//可以new多个request来支持多个域名请求
let $http = new request({
	//接口请求地址
	baseUrl: baseUrl,
	//服务器本地上传文件地址
	fileUrl: baseUrl,
	// 服务器上传图片默认url
	defaultUploadUrl: "api/common/v1/upload_image",
	//设置请求头（如果使用报错跨域问题，可能是content-type请求类型和后台那边设置的不一致）
	header: {
		'content-type': 'application/json;charset=UTF-8'
	},
	// 默认配置（可不写）
	config: {
		// 是否自动提示错误
		isPrompt: true,
		// 是否显示加载动画
		load: false,
		// 是否使用数据工厂
		isFactory: true
	}
});
// 

function sort_ASCII(obj, method) {
	var arr = new Array();
	var num = 0;
	for (var i in obj) {
		arr[num] = i;
		num++;
	}
	var sortArr = arr.sort();
	var sortObj = {};
	for (var i in sortArr) {
		if (sortArr[i] != "signature") {
			if (typeof(obj[sortArr[i]]) == 'number') {
				sortObj[sortArr[i]] = String(obj[sortArr[i]]);
			} else if (JSON.stringify(obj[sortArr[i]]) == "{}") {
				sortObj[sortArr[i]] = []
			} else if (obj[sortArr[i]] == undefined) {
				if (method == 'GET') {
					sortObj[sortArr[i]] = ""
				}
				// console.log(obj[sortArr[i]]);
				// if (obj[sortArr[i]] == null) {
				// 	sortObj[sortArr[i]] = ""
				// }
			} else {
				sortObj[sortArr[i]] = obj[sortArr[i]];
			}
		}
	}
	console.log(JSON.stringify(sortObj) + 'shesho_20210101');
	return JSON.stringify(sortObj) + 'shesho_20210101';
}


// 添加获取七牛云token的方法
$http.getQnToken = function(callback) {
	//该地址需要开发者自行配置（每个后台的接口风格都不一样）
	$http.get("api/kemean/aid/qn_upload").then(data => {
		/*
		 *接口返回参数：
		 *visitPrefix:访问文件的域名
		 *token:七牛云上传token
		 *folderPath:上传的文件夹
		 *region: 地区 默认为：SCN
		 */
		callback({
			visitPrefix: data.visitPrefix,
			token: data.token,
			folderPath: data.folderPath,
			region: "SCN"
		});
	});
}

//当前接口请求数
let requestNum = 0;
//请求开始拦截器
$http.requestStart = function(options) {
	if (options.load) {
		if (requestNum <= 0) {
			//打开加载动画
			uni.showLoading({
				title: '加载中',
				mask: true
			});
		}
		requestNum += 1;
	}
	// 图片上传大小限制
	if (options.method == "FILE" && options.maxSize) {
		// 文件最大字节: options.maxSize 可以在调用方法的时候加入参数
		const maxSize = options.maxSize;
		for (let item of options.files) {
			if (item.size > maxSize) {
				setTimeout(() => {
					uni.showToast({
						title: "图片过大，请重新上传",
						icon: "none"
					});
				}, 500);
				return false;
			}
		}
	}
	//请求前加入token

	// const token = store.state.token;
	// if(token) {
	// 	options.header['Authorization'] = 'Bearer '+ token;
	// }
	// 测试阶段用的token
	const userMessage = uni.getStorageSync('userMessage');
	options.header['Authorization'] = 'Bearer ' + userMessage.token;
	options.data['timestamp'] = Date.now()
	console.log(options.method);
	options.data['signature'] = md5Libs.md5(sort_ASCII(options.data, options.method))
	return options;
}
//请求结束
$http.requestEnd = function(options) {
	//判断当前接口是否需要加载动画
	if (options.load) {
		requestNum = requestNum - 1;
		if (requestNum <= 0) {
			uni.hideLoading();
		}
	}
}
//登录弹窗次数
let loginPopupNum = 0;
//所有接口数据处理（可在接口里设置不调用此方法）
//此方法需要开发者根据各自的接口返回类型修改，以下只是模板
$http.dataFactory = async function(res) {
	console.log("接口请求数据", {
		url: res.url,
		resolve: res.response,
		header: res.header,
		data: res.data,
		method: res.method,
	});
	if (res.response.statusCode && res.response.statusCode == 200) {
		let httpData = res.response.data;
		if (typeof(httpData) == "string") {
			httpData = JSON.parse(httpData);
		}
		/*********以下只是模板(及共参考)，需要开发者根据各自的接口返回类型修改*********/

		//判断数据是否请求成功
		if (httpData.code == 0) {
			// 返回正确的结果(then接受数据)
			return Promise.resolve(httpData.data);
		} else if (httpData.code == -1002 || httpData.code == -1001 || httpData.code == 1100) {
			let content = '此时此刻需要您登录喔~';
			if (loginPopupNum <= 0) {
				loginPopupNum++;
				uni.showModal({
					title: '温馨提示',
					content: content,
					confirmText: "去登录",
					cancelText: "再逛会",
					success: function(res) {
						loginPopupNum--;
						if (res.confirm) {
							uni.navigateTo({
								url: "/pages/user/login"
							});
						}
					}
				});
			}
			// 返回错误的结果(catch接受数据)
			return Promise.reject({
				statusCode: 0,
				errMsg: (httpData.info || httpData.msg)
			});
		} else { //其他错误提示
			if (res.isPrompt && typeof httpData.msg == 'string' && httpData.msg.trim() != '') {
				uni.showToast({
					title: httpData.info || httpData.msg,
					icon: "none",
					duration: 3000
				});
			}
			// 返回错误的结果(catch接受数据)
			return Promise.reject(httpData);
			// return Promise.reject({
			// 	statusCode: 0,
			// 	errMsg: (httpData.info || httpData.msg)
			// });
		}

		/*********以上只是模板(及共参考)，需要开发者根据各自的接口返回类型修改*********/

	} else {
		// 返回错误的结果(catch接受数据)
		return Promise.reject({
			statusCode: res.response.statusCode,
			errMsg: "【request】数据工厂验证不通过"
		});
	}
};
// 错误回调
$http.requestError = function(e) {
	// e.statusCode === 0 是参数效验错误抛出的
	if (e.statusCode === 0) {
		throw e;
	} else {
		uni.showToast({
			title: e,
			icon: "none"
		});
	}
}
export default $http;
